* {
    box-sizing: inherit;
}

$color1: #00f0f0;
$color2: #ff00f0;

html {
    background: linear-gradient(217deg, rgba($color2, 0.2), rgba(0, 0, 0, 0) 70.71%),
        linear-gradient(127deg, rgba($color1, 0.2), rgba(0, 0, 0, 0) 70.71%),
        linear-gradient(336deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0) 70.71%);
    background-color: rgb(11, 15, 17);
    background-repeat: no-repeat;
    background-attachment: fixed;
    height: 100%;
    overflow-x: hidden;
    --color: $color2;
}

body {
    box-sizing: border-box;
    font-family: 'Lato', sans-serif;
    font-size: 16px;
    color: rgb(241, 249, 255);
    font-weight: 100;
    --scroll-bg: transparent;
    --scroll-thumb: rgba($color1, 0.5);
}

body::-webkit-scrollbar {
    width: 4px;
}

body {
    scrollbar-width: thin;
    scrollbar-color: var(--scroll-thumb) var(--scroll-bg);
}

body::-webkit-scrollbar-track {
    background: var(--scroll-bg);
}

body::-webkit-scrollbar-thumb {
    background-color: var(--scroll-thumb);
}

a {
    color: var(--color);
    text-decoration: none;
    &:hover {
        text-decoration: underline;
    }
}

button {
    border: 0.4pt solid var(--color);
    color: var(--color);
    background: none;
    padding: 18px 30px;
    margin-top: 20px;
    font-family: inherit;
    font-size: 1.2em;
    cursor: pointer;
    transition: 150ms all ease;

    &:hover {
        box-shadow: 0 0 5px var(--color), inset 0 0 5px var(--color);
    }
}

h1 {
    font-weight: 300;
    font-size: 5em;
}

main {
    text-align: center;
}

.top {
    text-align: center;
    padding: 100px 0;
    margin-bottom: 200px;
}

.trail {
    width: 2px;
    height: 500px;
    border-radius: 0 0 10px 10px;
    background: linear-gradient(
        0deg,
        rgba(255, 255, 255, 0) 0%,
        rgba(255, 255, 255, 0.6) 1%,
        rgba(255, 255, 255, 0.2) 20%,
        rgba(255, 255, 255, 0) 100%
    );
    position: absolute;
    z-index: -1;
    top: -500px;
}

.container {
    max-width: 800px;
    margin: 0 auto;
    min-height: 1000px; // TODO remove
}
